<template>
  <el-dialog
    :title="dialogTitle"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="()=>$emit('callback', false)"
    class="dialog-box">
    <slot name="content"></slot>
    <span slot="footer">
      <slot name="footer"></slot>
    </span>
  </el-dialog>
</template>
<script>
  export default {
    name: 'memberDialog',
    props: {
      dialogVisible: {
        type: Boolean,
        default: false
      },
      dialogTitle: {
        type: String,
        default: ''
      }
    },
  }
</script>
