<template>
  <el-dialog
    title="修改密码"
    :visible.sync="dialogChangeVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="cancelDialog"
    class="dialog-box">
    <el-form :model="pwdForm" ref="pwdForm">
      <el-form-item label="手机号">
        <el-input :value="phone" readonly :placeholder="'input' | solvePlaceholder" />
      </el-form-item>
      <el-form-item label="验证码" prop="code">
        <el-input v-model="pwdForm.code" :placeholder="'input' | solvePlaceholder" >
          <template slot="append"><el-button type="primary" style="padding: 0 5px;"  @click="getChatCode">{{btnName}}</el-button></template>
        </el-input>
      </el-form-item>
      <el-form-item label="新手机号" prop="newPassword">
        <el-input v-model="pwdForm.newPassword" :placeholder="'input' | solvePlaceholder" />
      </el-form-item>
    </el-form>
    <span slot="footer">
      <el-button type="primary" @click="saveDialog">保存</el-button>
      <el-button @click="cancelDialog">取消</el-button>
    </span>
  </el-dialog>
</template>
<script>
  import {Apis} from "@/axios";
  import {Message} from 'element-ui'

  export default {
    name: 'changePwd',
    props: {
      dialogChangeVisible: {
        type: Boolean,
        default: false
      },
      phone: {
        type: String,
        default: null
      }
    },
    data() {
      return {
        pwdForm: {
          code:'',
          newPassword:''
        },
        btnName: '获取验证码',
        times: 60
      }
    },
    methods: {
      cancelDialog() {
        let _this = this
        _this.$refs['pwdForm'].resetFields()
        _this.$emit('callback', false)
      },
      saveDialog() {
        let _this = this
        let {changePassword} = Apis.member
        changePassword({..._this.pwdForm, phone: _this.phone}).then(res => {
          if (res.data.status == 200) {
            Message.success(res.data.msg)
            _this.$refs['pwdForm'].resetFields()
            _this.$emit('callback', false)
          } else Message.error(res.data.msg)
        })
      },
      getChatCode() {
        let _this = this
        let timer = setInterval(()=>{
          if (_this.times == 1) {
            _this.btnName = '获取验证码'
            _this.times = 60
            clearInterval(timer)
          } else {
            _this.times--
            _this.btnName = `${_this.times}s后重新获取`
          }
        },1000)
      }
    },

  }
</script>
