<template>
  <div class="member-box">
    <div class="query-wrapper">
      <div>
        <el-form :inline="true" :model="queryForm" ref="queryForm" label-width="100px">
          <el-form-item label="部门名称" prop="deptName">
            <el-autocomplete
              class="w100"
              v-model="queryForm.deptName"
              value-key="deptName"
              :fetch-suggestions="(queryString, cb) => queryInputAsync(queryString, cb, 'customer')"
              :placeholder="'input' | solvePlaceholder"
              @select="(val) => queryInputAsync('', val,'customer', true)"
            />
          </el-form-item>
          <el-form-item label="员工名称" prop="staffName">
            <el-input v-model="queryForm.staffName" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="员工手机号" prop="phone">
            <el-input v-model="queryForm.phone" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="员工状态" prop="status">
            <el-select v-model="queryForm.status" :placeholder="'select' | solvePlaceholder" class="w100">
              <el-option value="0" label="在职"></el-option>
              <el-option value="1" label="离职"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="创建时间" prop="createTime">
            <el-date-picker
              style="width:100%;"
              v-model="queryForm.createTime"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期" />
          </el-form-item>
        </el-form>
      </div>
      <div class="query-btn-group mt15">
        <el-button type="primary" @click="queryContent('query', pages)">查询</el-button>
        <el-button @click="reset('queryForm')">重置</el-button>
        <el-button @click="()=>{dialogFormVisible=true;dialogTitle='新增'}">新增</el-button>
      </div>
    </div>
    <el-table :data="staffs">
      <el-table-column prop="staffId" label="员工编号" />
      <el-table-column prop="staffName" label="员工名称" />
      <el-table-column prop="departName" label="部门名称" />
      <el-table-column prop="phone" label="手机号" />
      <el-table-column prop="status" label="在职状态" />
      <el-table-column label="创建时间" >
        <template slot-scope="scope">{{scope.row.gmtCreateTime | solveTime}}</template>
      </el-table-column>
      <el-table-column  label="操作" width="180">
        <template slot-scope="scope">
          <el-button type="text" size="mini" @click="tableOpera('change', scope.row)">修改</el-button>
          <el-button type="text" size="mini" @click="tableOpera('delete', scope.row)">删除</el-button>
          <el-button type="text" size="mini" @click="tableOpera('quit', scope.row)">离职</el-button>
          <el-button type="text" size="mini" @click="tableOpera('dispatch', scope.row)">派工单</el-button>
          <el-button type="text" size="mini" @click="tableOpera('resetPwd', scope.row)">重置密码</el-button>
          <el-button type="text" size="mini" @click="tableOpera('prohibitLogin', scope.row)">禁止登录</el-button>
        </template>
      </el-table-column>
    </el-table>

    <MemberPagination @callback="queryContent('query')" :total="total" :pages="pages"/>

    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :before-close="()=>cancelStaffs('staffsForm')"
    >
      <el-form :model="staffsForm" ref="staffsForm">
        <el-form-item label="员工名称" prop="name">
          <el-input v-model="staffsForm.name" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="归属部门" prop="deptId">
          <el-select v-model="staffsForm.deptId" :placeholder="'select' | solvePlaceholder"  class="w100">
            <el-option v-for="tItem in trees" :key="tItem | solveKey" :value="tItem.id" :label="tItem.deptName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="手机号" prop="phone" >
          <el-input v-model="staffsForm.phone" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="登录密码" prop="password">
          <el-input v-model="staffsForm.password" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="staffsForm.sex" :placeholder="'select' | solvePlaceholder" class="w100">
            <el-option value="0" label="男"/>
            <el-option value="1" label="女"/>
          </el-select>
        </el-form-item>
        <el-form-item label="角色" prop="roleId">
          <el-select v-model="staffsForm.roleId" :placeholder="'select' | solvePlaceholder"  class="w100">
            <el-option v-for="rItem in roles" :key="rItem | solveKey" :value="rItem.id" :label="rItem.alias"/>
          </el-select>
        </el-form-item>
        <el-form-item label="员工状态" prop="status">
          <el-select v-model="staffsForm.status" :placeholder="'select' | solvePlaceholder"  class="w100">
            <el-option v-for="(sItem,index) in staffStatus" :key="sItem | solveKey" :value="index" :label="sItem"/>
          </el-select>
        </el-form-item>
      </el-form>

      <span slot="footer">
        <el-button type="primary" @click="saveStaffs('staffsForm')">保存</el-button>
        <el-button @click="cancelStaffs('staffsForm')">取消</el-button>
      </span>
    </el-dialog>
    <ChangePwd :phone="phone" :dialogChangeVisible="dialogChangeVisible" @callback="isOpen=>dialogChangeVisible=isOpen"/>
    <MemberDialog :dialogVisible="dipacthVisible" :dialogTitle="'派遣工单'" @callback="isOpen => dipacthVisible = isOpen">
      <template v-slot:content>
        <el-form :model="dispatchForm" ref="dispatchForm">
        <el-form-item label="员工姓名" prop="statffName">
          <el-input v-model="dispatchForm.statffName" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
        <el-form-item label="工单类型" prop="type">
          <el-select v-model="dispatchForm.type" :placeholder="'select' | solvePlaceholder" class="w100">
            <el-option label="跟进客户" value="1"/>
          </el-select>
        </el-form-item>
        <el-form-item label="工单级别" prop="prior">
          <el-select v-model="dispatchForm.prior" :placeholder="'select' | solvePlaceholder" class="w100">
            <el-option label="紧急" value="1"/>
            <el-option label="一般" value="2"/>
            <el-option label="暂缓" value="3"/>
          </el-select>
        </el-form-item>
        <el-form-item label="工单内容" prop="task">
          <el-input type="textarea" :row="2" :maxlength="1000" v-model="dispatchForm.task" :placeholder="'input' | solvePlaceholder" />
        </el-form-item>
      </el-form>
      </template>
      <template v-slot:footer>
        <el-button type="primary" @click="saveDispatch('dispatchForm')">派遣</el-button>
        <el-button @click="cancelDispatch('dispatchForm')">取消</el-button>
      </template>
    </MemberDialog>
  </div>
</template>
<script>
  import {Apis} from '@/axios'
  import MemberPagination from '@/components/memberPagination'
  import ChangePwd from '@/components/changePwd'
  import MemberDialog from '@/components/memberDialog'
  import {Message} from "element-ui";
  import {removeDuplicateObj, treeToArray, dateFtt} from "@/utils/tools";
  import store from '@/store'

  export default {
    name: 'Staffs',
    data() {
      return {
        queryForm: {
          deptName:'',
          createTime:'',
          phone:'',
          status:'',
          staffName:'',
        },
        queryFormCopy:{},
        staffs: [],
        belongDeparts: [],
        dialogFormVisible: false,
        dialogTitle:'',
        staffsForm: {
          name:'',
          departId:'',
          password:'',
          phone:'',
          status:'',
          sex:'',
          roleId:''
        },
        pages: {
          pageNum: 1,
          pageSize: 10
        },
        total: null,
        roles:[],
        phone: '',
        dialogChangeVisible: false,
        trees:[],
        staffStatus:['在职', '在职禁止登录', '离职', '删除'],
        dipacthVisible:false,
        dispatchForm: {
          statffName:'',
          type:'',
          prior:'',
          task:''
        }
      }
    },
    components:{MemberPagination, ChangePwd, MemberDialog},
    mounted() {
      let _this = this
      _this.init()
    },
    methods: {
      init() {
        let _this = this
        _this.queryContent('query', _this.pages)
        _this.getRoles()
        _this.departTree()
      },
      queryInputAsync(keyWord, cb, type, isSelect) {
        let _this = this
        let {deptTree} = Apis.member
        if (isSelect) {
          let {id} = cb
          _this.queryForm.deptId = id
        } else {
          deptTree().then(res => {
            if (res.data.status == 200) {
              let trees = treeToArray(res.data.data)
              let data = removeDuplicateObj(trees, 'id')
              cb(data)
            }
            else Message.error(res.data.msg)
          })
        }
      },
      queryContent(type, _pages) {
        let _this = this
        let {flowerCustomerPage} = Apis.member
        let params = type == 'query' ? {..._this.queryForm} : type == 'pagination' ? {..._this.queryFormCopy}: {}
        params = _pages ? {...params, ..._pages} : {...params, ..._this.pages}
        if(_this.queryForm.createTime[0])params.startDate = dateFtt(_this.queryForm.createTime[0])
        if(_this.queryForm.createTime[1])params.endDate = dateFtt(_this.queryForm.createTime[1])
        flowerCustomerPage(params).then(res => {
          if (res.data.status == 200) {
            let {records, total} = res.data.data || {}
            _this.total = total
            _this.staffs = records
          } else Message.error(res.data.msg)
        })
      },
      reset(formRef) {
        let _this = this
        _this.$refs[formRef].resetFields()
      },
      tableOpera(type, obj) {
        let _this = this
        let {banOrFire} = Apis.member
        let {staffId, phone} = obj
        switch (type) {
          case 'change':
            _this.dialogFormVisible=true;
            _this.dialogTitle='修改'
            let tx = {..._this.staffsForm, ...JSON.parse(JSON.stringify(obj))}
            console.log('tx', tx)
            break;
          case 'delete':
          case 'prohibitLogin':
          case 'quit':
            _this.$confirm('是否操作此数据?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              let status = type == 'delete'?3:type == 'prohibitLogin'?1:type=='quit'?2:null
              banOrFire({staffId, status}).then(res => {
                if (res.data.status == 200) {
                  Message.success(res.data.msg)
                  _this.queryContent('query', _this.pages)
                } else Message.error(res.data.msg)
              })
            }).catch(() => {
              _this.$message({
                type: 'info',
                message: '已取消删除'
              });
            });
            break;
          case 'dispatch':
            _this.dispatchForm.staffId = staffId
            _this.dipacthVisible = true
            break;
          case 'resetPwd':
            _this.phone = phone
            _this.dialogChangeVisible = true
            break;
          default:
            break
        }
      },
      saveStaffs(formRef) {
        let _this = this
        let {addStaff} = Apis.member
        addStaff(_this.staffsForm).then(res => {
          if (res.data.status == 200) {
            Message.success(res.data.msg)
            _this.queryContent('query', _this.pages)
            _this.$refs[formRef].resetFields()
            _this.dialogFormVisible = false
          } else Message.error(res.data.msg)
        })
      },
      cancelStaffs(formRef) {
        let _this = this
        _this.$refs[formRef].resetFields()
        _this.dialogFormVisible = false
      },
      getRoles() {
        let _this = this
        let {getRoles} = Apis.member
        getRoles({roleId: store.state.user.userInfo.roleId}).then(res => {
          if (res.data.status == 200) {
            _this.roles = res.data.data
          } else Message.error(res.data.msg)
        })
      },
      departTree(){
        let _this = this
        let { deptTree } = Apis.member
        deptTree().then(res => {
          if (res.data.status == 200) {
            let trees = treeToArray(res.data.data)
            _this.trees = removeDuplicateObj(trees, 'id')
          } else Message.error(res.data.msg)
        })
      },
      saveDispatch(formRef) {
        let _this = this
        let {handlerTask} = Apis.member
        handlerTask(_this.dispatchForm).then(res => {
          if (res.data.status == 200) {
            Message.success(res.data.msg)
            _this.queryContent('query', _this.pages)
            _this.dipacthVisible = false
            _this.$refs[formRef].resetFields()
          } else Message.error(res.data.msg)
        })
      },
      cancelDispatch(formRef) {
        let _this = this
        _this.dipacthVisible = false
        _this.$refs[formRef].resetFields()
      }
    },
  }
</script>
<style lang="less" scoped>
  .member-box {
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    .query-wrapper{
      margin-bottom: 30px;
    }
    .query-btn-group{
      text-align: right;
    }
  }
</style>
